.To_do_list {
  width: 30vw;
  height: 75vh;
  position: relative;
  background-color: beige;
  border: 2px solid rgb(124, 124, 0);
  padding: 10px;
}
header {
  text-align: center;
  font-size: 2rem;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
}

.add_task_button {
  position: absolute;
  margin: 20px;
  text-align: center;
  right: 0;
  bottom: 0;
  cursor: pointer;
}

.todo_form{
  width: 80%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
  
}
