.todo_form {
  text-align: center;
  margin: auto;
  position: relative;
  height: 350px;
}
.todo_input {
  border: none;
  background-color: rgb(255, 255, 178);
  height: 90%;
  border-radius: 10px;
  width: 100%;
}
.todo_button {
  position:absolute;
  right:0;
  bottom:40px;
  cursor: pointer;
  border: none;
  text-align: center;
  background-color: transparent;
}
textarea {
  resize: none;
  outline: none;
  font-size: 1.4rem;
}