.todo-row {
  width: 100%;

  border-radius: 10px;
  background-color: rgb(255, 148, 25);
  margin-top: 10px;
  color: white;
  text-align: center;
}
.todo_text {
  word-wrap: break-word;
  margin-left: 10px;
  font-size: 1.5rem;
  padding: 5px;
  margin:auto;
}
.icons {
  align-items: center;
  font-size: 30px;
  cursor: pointer;
}
.delete-icon:hover{
    color: rgb(255, 0, 0)
}
.edit-icon:hover{
  color: rgb(0, 255, 34)
}
